.projects {
  padding-top: 6rem;
  padding-bottom: 6rem;
}
.p-title {
  font-size: 3rem;
}

.cards {
  display: grid;
  grid-template-columns: auto auto auto;
}

.card {
  transition: 0.3s;
  position: relative;
  overflow: hidden;
  z-index: 1;
  margin: 10px;
}

.hover-link {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  flex-direction: column;
  width: 100%;
  height: 100%;
  color: #fff;
  transition: all 0.5s;
  background-color: rgba(28, 153, 255, 0.507);
  opacity: 0;
}

.hover-link > svg {
  color: white;
}

.hover-link:hover {
  opacity: 1;
}

.link-container {
  display: flex;
  width: 80px;
  justify-content: space-between;
}

@media (max-width: 600px) {
  .cards {
    grid-template-columns: repeat(1, 1fr);
  }
}
